.main-nav__toggle {
  position: absolute;
  top: -27px;
  right: 30px;
  display: block;
  cursor: pointer;
  border: none;
  width: 20px;
  height: 14px;
  background-color: inherit;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 2px;
  background-color: #231F20;
  box-shadow: 0 6px 0 0 #231F20, 0 12px 0 0 #231F20;
}
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 6px;
  left: -4px;
  width: 28px;
  height: 2px;
  background-color: #231F20;
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 6px;
  left: -4px;
  width: 28px;
  height: 2px;
  background-color: #231F20;
  box-shadow: none;
  transform: rotate(45deg);
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .user-list__item {
    display: none;
  }
  .main-nav--opened .main-nav__content {
    position: absolute;
    width: 100%;
    top: 19px;
    z-index: 10;
  }
  .main-nav--nojs .page-header__content {
    border-bottom: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__content {
    position: static;
    min-height: 0;
    margin-top: 19px;
  }
  .main-nav--nojs .site-list__item {
    display: flex;
  }
  .main-nav--nojs .user-list__item {
    display: flex;
  }
}
